<template>
  <div class="wrapper_newsletter" :style="[newsletter, settingGeneral]">
    <div
      :style="[
        {
          '--font-style-1':
            this.settingGeneral && this.settingGeneral.fount_1
              ? this.settingGeneral.fount_1
              : 'Poppins',
        },
        {
          '--font-style-2':
            this.settingGeneral && this.settingGeneral.fount_2
              ? this.settingGeneral.fount_2
              : 'Roboto',
        },
      ]"
    >
      <div class="wrapper-items-content">
        <div class="product-text">
          <div class="product-tittle">
            <span class="tittle">Obtenga las últimas</span>
            <span class="tittle">ofertas y más.</span>
          </div>
        </div>
      </div>
      <div class="wrapper-items">
        <div class="conten-input-check">
          <div class="content-input">
            <ValidationProvider
              ref="validate"
              name="email"
              rules="required|email"
              class="content-input-error"
            >
              <template slot-scope="{ errors }">
                <div class="input-icon">
                  <div>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      xmlns:xlink="http://www.w3.org/1999/xlink"
                      version="1.1"
                      width="100%"
                      height="100%"
                      viewBox="0 0 24 24"
                      color="#2c2930"
                      class="icon-msg"
                    >
                      <path
                        d="M22 6C22 4.9 21.1 4 20 4H4C2.9 4 2 4.9 2 6V18C2 19.1 2.9 20 4 20H20C21.1 20 22 19.1 22 18V6M20 6L12 11L4 6H20M20 18H4V8L12 13L20 8V18Z"
                      />
                    </svg>
                  </div>
                  <input
                    name="email"
                    class="input-text"
                    type="email"
                    :placeholder="$t('newsletter_email')"
                    v-model="email"
                    id="CorreoElectronicoNewslletter"
                  />
                </div>
                <span
                  v-show="errors[0] || register"
                  class="text-error"
                  :style="register ? 'color:green' : ''"
                  >{{ errorsCheckbox || register }}</span
                >
              </template>
            </ValidationProvider>
          </div>
          <div class="content-checkbox">
            <input type="checkbox" id="checkbox" v-model="checked" />
            <p class="text-checkbox">
              {{ $t('newsletter_msg') }}
            </p>
          </div>
          <p class="text-error" v-if="stateChehed">
            Marcar checkbox para poder suscribirse al boletín informativo
          </p>
        </div>

        <div class="content-button">
          <button
            ref="colorBtn"
            @click="submitNewsletter"
            class="content-bttns-shop"
          >
            <span class="content-textbutton-shop">
              {{ $t('newsletter_btn') }}
            </span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
export default {
  name: 'Ko-Newsletter-1',
  props: {
    dataStore: Object,
    newsletter: Object,
    settingGeneral: Object,
  },
  components: {
    ValidationObserver,
    ValidationProvider,
  },
  mounted() {},
  data() {
    return {
      errorsCheckbox: 'El campo checkbox y email son obligatorios',
      email: null,
      register: '',
      checked: false,
      stateChehed: false,
    }
  },
  destroyed() {
    this.email = ''
  },
  computed: {
    facebookPixel() {
      return this.$store.state.analytics_tagmanager
    },
  },
  methods: {
    submitNewsletter() {
      if (this.checked == true) {
        this.$refs.validate
          .validate()
          .then((response) => {
            if (response.valid) {
              const json = {
                email: this.email,
                tienda: this.dataStore.tienda.id_tienda,
              }
              axios
                .post(
                  `${this.$store.state.urlKomercia}/api/tienda/suscriptor`,
                  json
                )
                .then((res) => {
                  if (
                    this.facebookPixel &&
                    this.facebookPixel.pixel_facebook != null
                  ) {
                    window.fbq('track', 'Lead', { value: this.email })
                  }
                  this.register = 'Tu correo ha sido registrado'
                  this.$message.success('Comentario enviado!')
                  this.email = ''
                })
                .catch(
                  (res) => (
                    (this.register = 'Tu correo ya esta registrado'),
                    this.$message.error('Tu correo ya esta registrado')
                  )
                )
            }
          })
          .catch((e) => {
            console.log(e)
          })
      } else {
        this.stateChehed = true
      }
    },
  },
  watch: {},
}
</script>

